
#app {
  font-family             : 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing  : antialiased;
  -moz-osx-font-smoothing : grayscale;
  text-align              : center;
  color                   : #2C3E50;
  /*margin-top              : 60px;*/
}

.panel {
  overflow : auto;
  position : relative;
}
i {
  color : #777777;
}
.section__subtitle {
  margin-bottom : 40px;
}
#summary, #avis {
  background-color : #FFFFFF;
  border           : 1px solid #CCCCCC;
  border-radius    : 2px;
  float            : left;
  padding          : 30px 20px;
}
#summary {
  float      : left;
  width      : 30%;
  text-align : left;
}
#avis {
  float : right;
  width : 68%;
}
.site-list {
  list-style : none;
}

.bc-wrapper {
  border        : solid 1px #CCCCCC;
  border-radius : 2px;
  display       : inline-block;
  margin        : 0 7px 30px 7px;
}
.bc-label {
  border-bottom    : solid 1px #CCCCCC;
  background-color : #F3F3F3;
  padding          : 10px;
}
.bc-checkbox {
  margin        : 10px;
  display       : inline-block;
  border-radius : 6px;
  width         : 150px;
  height        : 150px;
  font-size     : 7em;
  padding       : 10px 19px;
}
.bc-red {
  background : hsl(0, 50%, 90%);
  color      : hsl(0, 50%, 50%);
}
.bc-orange {
  background : hsl(30, 66%, 90%);
  color      : hsl(30, 66%, 50%);
}
.bc-yellow {
  background : hsl(55, 66%, 90%);
  color      : hsl(55, 66%, 60%);
}
.bc-green {
  background : hsl(125, 50%, 90%);
  color      : hsl(125, 50%, 50%);
}

.kr-group {
  position : relative;
}
.kr-group label {
  position       : absolute;
  z-index        : 7;
  top            : 24px;
  left           : 0;
  pointer-events : none;
  -webkit-transition     : all .1s;
  transition     : all .1s;
}
.kr-group label.top {
  color     : #555555;
  position  : absolute;
  font-size : 0.7em;
  top       : 5px;
  left      : 0;
}
.kr-group label.focus {
  color : #655FFF;
}
.kr-group label.error {
  color : #CC5250;
}
.kr-group input {
  border-radius    : unset;
  border           : none;
  border-bottom    : solid 1px #CCCCCC;
  margin-bottom    : 24px;
  padding          : 20px 0 3px;
  background-color : #FFFFFF;
  z-index          : 5;
}
.kr-group input:focus {
  border-bottom : solid 2px #655FFF;
  margin-bottom : 23px;
}
.kr-group input.error {
  border-bottom-color : #CC5250;
}
.kr-clear {
  background : none;
  border     : none;
  position   : absolute;
  right      : 8px;
  top        : 15px;
  padding    : 8px 12px;
  width      : 32px;
  height     : 32px;
  z-index    : 7;
  color      : #333333;
}
.kr-helper {
  color      : #888888;
  font-size  : 0.7em;
  position   : absolute;
  text-align : left;
  left       : 0;
  z-index    : 2;
  top        : 50px;
}
.kr-helper li {
  list-style : none;
}
.kr-helper.info {
  color : #52A3CC;
}
.kr-helper.warning {
  color : #CC7750;
}
.kr-helper.error {
  color : #CC5250;
}
.kr-autocomplete-options-wrapper,
.kr-loading-wrapper,
.kr-no-results-wrapper {
  position : absolute;
  top      : 50px;
  left     : -20px;
  z-index  : 10;
  overflow : hidden;
}
.kr-autocomplete-options,
.kr-loading,
div.kr-no-results {
  background-color : #FFFFFF;
  margin           : 0 20px 20px 20px;
  padding          : 10px;
  -webkit-box-shadow       : #999999 0 0 20px;
          box-shadow       : #999999 0 0 20px;
  border           : 1px solid #CCCCCC;
  min-width        : 250px;
}
.kr-loading {
  font-size  : 1.5em;
  min-height : 40px;
  text-align : left;
}
.kr-autocomplete-options {
  overflow-y : auto;
  max-height : 250px;
  padding    : 10px 0;
}
.kr-autocomplete-option {
  list-style       : none;
  text-align       : left;
  padding          : 8px;
  background-color : #FFFFFF;
  -webkit-transition       : all 0.1s;
  transition       : all 0.1s;
}
.kr-autocomplete-option.is-active {
  background-color : #CCCCCC;
}
.kr-autocomplete-option:hover {
  background-color : #CCCCCC;
}
.kr-clear:hover {
  background-color : #CCCCCC;
  border-radius    : 17px;
}

